            dataName: "app_mem_used",
            color: "green",
            backgroundColor: "#26B56F",
          },
        ],
      };
    },
    handletabClick(value){
      if(value==='cpu'){
        this.isActiveClass = true;
        this.showCPUchart = true;
      }else{
        this.showCPUchart = false;
        this.isActiveClass = false;
      }
    },
    showNoData(){
            console.error('this.showCPUchart',this.showCPUchart);
            console.error('this.cpuUsageInfoList',!this.cpuUsageInfoList.some(item=>item.cpu_usage_app != 0));
            let result = true;    
            if(this.showCPUchart){
                if(this.cpuUsageInfoList.length == 0 || !this.cpuUsageInfoList.some(item=>item.cpu_usage_app != 0)){
                   console.error('showNoData');
                    result = true;
                }else{
                     result = false;
                }
            }else{
                if(this.memoryUsageInfoList.length == 0 || !this.memoryUsageInfoList.some(item=>item.memory_usage_app != 0)){
                      result = true;
                }else{
                     result = false;
                }
            }
            console.error('showNoData',result);
            return result;
    }
  },
  computed: {
    option() {
      let [x, y, z] = this.InitData(this.cpuUsageInfoList);
      
      // // 安卓部分手机拿不到cpu使用率
      // if(!this.cpuUsageInfoList.some(item=>item.cpu_usage_app != 0) && JsBridge.platform === 8){
      //     // this.performList.splice(0,1);
      //     // this.isActiveClass = true;
      //     // this.showCPUchart = true;
      // }
       
      //console.error('x,y,z',x,y,z);
      return this.getOption(x, y, z);
    },
    optionMemory() {
      let [x, y, z, w] = this.InitMemeoryData(this.memoryUsageInfoList);
      // console.error('x,y,z,w',x,y,z,w)
      return this.getMemoryOption(x, y, z, w);
    },
  },
};
</script>

