    // 房间耗时
    async timeConsume() {
      let res = await JsBridge.timeConsume(this.uid, this.room_id);
      console.error("timeConsume", JSON.stringify(res));
      let i = 0;
      // console.log('i2324',res[0]);
      for (i = 0; i < res.length; i++) {
        if (res[i].type === "1") {
          this.processing_time = res[i].processing_time;
          this.startTimeProcessing_time = res[i].timestamp;
          // console.log('登录（进房）时间,开始时间戳',this.processing_time,this.startTimeProcessing_time);
          this.roomInfoList[4].value = this.processing_time;
        }
        if (res[i].type === "2") {
          this.endTimeProcessing_time = res[i].timestamp;
          // console.log('endTimeProcessing_time',this.endTimeProcessing_time);
        }
        this.fangjianhaoshi =
          this.endTimeProcessing_time - this.startTimeProcessing_time;
        this.roomInfoList[0].value = this.fangjianhaoshi;
      }
      // console.log(' this.fangjianhaoshi', this.fangjianhaoshi);
      this.firstFrameList = res.filter(
        (item) => item.type === "5" && item.processing_time
      );
      console.error("firstFrameList", JSON.stringify(this.firstFrameList));
      let time_all = 0;
      for (let i = 0; i < this.firstFrameList.length; i++) {
        time_all += this.firstFrameList[i].processing_time * 1;
      }
      this.firstFrameList.length > 0 &&
        (this.firstFrameTime = Math.round(
          time_all / this.firstFrameList.length
        ));
      console.error("首帧耗时：", this.firstFrameTime);
      this.roomInfoList[3].value = this.firstFrameTime;
    },
    // 互动时长
    async timeInteractiveStart() {
      // 查询互动时长
      let res = await JsBridge.timeInteractiveStart(this.uid, this.room_id);
      if(res[0].timestamp && res[1].timestamp  ){
            this.timeInteract = res[1].timestamp - res[0].timestamp;
           this.roomInfoList[1].value = this.timeInteract;
           console.error("timeInteractiveStart", res[1].timestamp,res[0].timestamp,":s");
      }
    },
    // 端到端延迟
    async breakRateEnd2endDelay() {
      let res = await JsBridge.breakRateEnd2endDelay(this.uid, this.room_id);
      // this.peer_to_peer_time =res.map[]
      // this.peer_to_peer_time
      console.error(
        this.$t("qualityReport.index.5a1ho2twyig0"),
        JSON.stringify(res)
      );
      let sum = 0;
      if (res.length === 0) {
        this.peer_to_peer_time = 0;
      } else {
        for (let i = 0; i < res.length; i++) {
          // debugger;

          // console.log('ityem:',res[i]);
          sum = sum + res[i].peer_to_peer_delay * 1;
        }
        this.peer_to_peer_time = Math.round(sum / res.length);
      }

      // console.log("端到端延迟数据breakRateEnd2endDelay", res);
      this.roomInfoList[2].value = this.peer_to_peer_time;
    },
    // 房间时长

    //异常事件
    async errorEvent() {
      let res = await JsBridge.errorEvent(this.uid, this.room_id);
      // console.log("errorEvent", res);
      res.forEach((item) => {
        let time = new Date(item.timestamp * 1000);
        var h = time.getHours(); // getHours方法返回 Date 对象的小时 (0 ~ 23)
        var m = time.getMinutes(); // getMinutes方法返回 Date 对象的分钟 (0 ~ 59)
        if(h<10){
          h = '0'+h;
        }
        if(m<10){
          m='0'+m;
        }
        item.timestamp = h + ":" + m;
        return item;
      });
      this.roomErrorInfoList = res;
    },
