<template>
    <div class="feed-wrapper">
        <div class="feed-wrapper-title">
          <img src="./images/pic_left.png" alt="">
          <span>{{$t('scoring.FeedBackOption.5a30930l4i00')}}</span>
          <img src="./images/pic_left.png" alt="" class="right">
        </div>
          
        <div class="improve" :class="isEn?'is-en':''">
           <el-checkbox-group v-model="checkList" v-for="(item,index) in opinionList" :key="index+'1'" :class="{ select: selectIndex === index }" class="improve-wrapper" @change="handleSubmitScore2">
               <el-checkbox :label="item.content" ></el-checkbox>
          </el-checkbox-group>
        </div>
        <div class="question-desc">
          <el-input
              type="textarea"
              @change="handleSubmitScore"
              v-model="descString"
              :placeholder="$t('scoring.FeedBackOption.5a31umy5e1w0')"
              maxlength="150"
              class="desc"
              @input="descInput"
          ></el-input>
          <span class="words-number" :class="maxNum?'max-number':''"
          ><span class="total-words">{{ wordsSum }}</span
          >/150</span
        >
        </div>
    </div>
</template>
<script>
export default {
  name: 'feedback',
  props: {
    showfeedback: {
      type: Boolean,
      default: false
    },
    opinionList: {
      type: Array,
      default: function() {
        return [];
      },
    },
    isEn:{
      type:Boolean,
      default:false
    }
  },
  data(){
    return{
      descString: "", // 问题描述
      selectIndex: null,
      // 反馈的问题列表
      checkList:[],
      checkInfo:'',
      wordsSum:0,
      maxNum:false,
    }
  },
   watch: {
    wordsSum:function(){
      if(this.wordsSum>=150){
        this.maxNum = true;
      }
      else{
        this.maxNum =false;
      }
    }
  },
  created(){
  },
  methods: {
    handleSubmitScore(){
        this.handleValue();
    },
    handleSubmitScore2(){
      this.handleValue();
    },
    handleValue(){
      for(let i=0;i<this.checkList.length;i++){
        
        this.checkInfo = this.checkList.join(',');
      }
      this.$emit('checkInfo', this.checkInfo);
      this.$emit('descString',this.descString);
    },
    descInput() {
      if(this.descString.length>150)
      {
        this.descString = this.descString.substring(0,150);
      }
      this.wordsSum = this.descString.length;

    },
  },
  computed:{
  }
}
</script>

