import Vue from 'vue'
import App from './App'
import router from './router/'
import ElementUI from 'element-ui'
import VueRouter from 'vue-router'
import VueI18n from 'vue-i18n' 




import 'element-ui/lib/theme-chalk/index.css'
import "./assets/js/respond.js";
 
import ECharts from 'vue-echarts'
import { use } from 'echarts/core'
// 手动引入 ECharts 各模块来减小打包体积

import {
  CanvasRenderer
} from 'echarts/renderers'
import { VisualMapComponent } from 'echarts/components';
import {
  BarChart
} from 'echarts/charts'
import {
  GridComponent,
  TooltipComponent
} from 'echarts/components'


import  zh   from './locales/zh-cn.json'    // 引入lang文件
import  en   from './locales/en.json'

use([
  CanvasRenderer,
  BarChart,
  GridComponent,
  TooltipComponent,
  VisualMapComponent
]);

Vue.use(VueI18n)     // 使用VueI18n
const i18n = new VueI18n({
  locale: 'zh',   // 设置语言
  messages: {
    zh: { ...zh },
    en: { ...en }
  }
})


Vue.use(VueRouter);
const defaultTitle = 'Zego'
router.beforeEach((to, from, next) => {
 if(location.href.indexOf('lang=en')>-1){
     i18n.locale = 'en';
 } 
 document.title = to.meta.title ? i18n.t(to.meta.title)
   : defaultTitle
 next()
})



// 全局注册组件（也可以使用局部注册）
Vue.component('v-chart', ECharts)


Vue.use(ElementUI)
// import WaterMarker from '@/components/waterMarker/to'

Vue.config.productionTip = false
// Vue.use(new WaterMarker())

new Vue({
  router,
  i18n,
  render: h => h(App),
}).$mount('#app')
