<template>
  <div class="scoring-demo">
    <div class="box-title">{{$t('scoring.index.5a1htpgs5zk0')}}</div>
    <scoring
      v-model="scoring3"
      :disabled="disabled3"
      :text="scoreDetailList[0]"
      class="zhengti"
    ></scoring>
    <FeedBackOption :isEn="isEn" :showfeedback="showfeedback" :opinionList="opinionList" v-if="showfeedback" @checkInfo="handlecheckList" @descString="handledescString"/>
    <div class="abcdef">
      <div
        class="handle-submit-score"
        :class="`${isNotClick ? 'is-full-star' : 'is-not-full'}`"
        @click="handleSubmitScore()"
      >
        {{$t('scoring.index.5a1htpgs7ps0')}}
      </div>
    </div>
    <!-- </div> -->
  </div>
</template>

<script>
import scoring from "./to";
import axios from "axios";
import FeedBackOption from './FeedBackOption.vue';
export default {
  data() {
    return {
      scoreDetailList: [],
      showmore: false,
      videoScore: 0,
      funScore: 0,
      demoScore: 0,
      isNotClick: false,
      scoring0: 0,
      scoring1: 4,
      scoring2: 2,
      scoringText2: [1, 2, 3, 4, 5],
      scoring3: 0,
      scoringText3: ["1", "2", "3", "4", "5"],
      disabled3: false,
      scoring4: 5,
      icon4: "apple",
      scoring5: 2,
      icon5: "icon5",
      voidIcon5: "voidIcon5",
      scoring6: 5,
      size6: "5rem",
      scoring7: 3,
      color7: "rgb(66, 255, 38)",
      voidColor7: "#ff2626",
      scoring8: 7,
      num8: 8,
      scoring9: 8,
      disabled9: false,
      icon9: "icon9",
      voidIcon9: "voidIcon9",
      size9: "32px",
      color9: "#9326ff",
      voidColor9: "white",
      num9: 9,
      uid: "",
      roomid: "",
      name: "",
      product: "",
      platform: "",
      appVersionStr: "",
      appVersion: 1.0,
      query: {},
      showfeedback:false,
      opinionList:[
        {
          index:'1',
          content:this.$t('scoring.index.5a305d4ceqs0')
        },
        {
          index:'2',
          content:this.$t('scoring.index.5a305d4cfk00')
        },
        {
          index:'3',
          content:this.$t('scoring.index.5a305d4cfrw0')
        },
        {
          index:'4',
          content:this.$t('scoring.index.5a305d4cfxw0')
        },
        {
          index:'5',
          content:this.$t('scoring.index.5a305d4cg380')
        },
        {
          index:'6',
          content:this.$t('scoring.index.5a305d4cgc00')
        },
        {
          index:'7',
          content:this.$t('scoring.index.5a305d4cglw0')
        },
        {
          index:'8',
          content:this.$t('scoring.index.5a305d4cgr80')
        },
        {
          index:'9',
          content:this.$t('scoring.index.5a305d4cgwg0')
        }
      ],
      checkInfo:'',
      descString:'',
      isEn:false,
    };
  },
  components: {
    scoring,
    FeedBackOption
  },
  methods: {
    handlecheckList(value){
      this.checkInfo = value;
    },
    handledescString(value){
      this.descString = value;
    },
    // 提交评分
    handleSubmitScore() {
       let data = {
        uid: this.uid,
        room_id: this.roomid,
        account: "",
        name: this.name,
        client: "",
        product: this.product,
        evaluate_type: this.checkInfo,
        more:this.descString, //更多评价
        score_verion: 1,
        score: [
          {
            index: 1,
            value: this.scoring3,
          }
        ],
        common_data: {
          device_id: "",
          system_version: "",
          platform: this.platform,
          app_version: this.appVersion,
          app_version_string: this.appVersionStr,
          dist_no: 0,
        },
      };
     // axios.post('http://192.168.6.26:7001/scoring',data)
      axios
        .post("https://edusuite-feedback-general.zego.im/scoring", data)
        .then((response) => {
          const { ret } = response.data;
          const { code, message } = ret;
          if (code === 0) {
            this.$message({
              message: this.$t('scoring.index.5a1htpgs8og0'),
              type: "success",
              center: true,
              duration:1000
            });
          } else {
            console.error(message);
            this.$message({
              message: this.$t('scoring.index.5a1htpgs8ro0'),
              type: "error",
              center: true,
              customClass: "error-msg",
              duration:1000
            });
          }
          setTimeout(()=>{
            this.$router.push({ name: "quality-report", query: this.query });
          },1000)
          
        })
        .catch((err) => {
          console.log("err", err);
          console.log(this.$t('scoring.index.5a1htpgs8ug0'));
          this.$router.push({ name: "quality-report", query: this.query });
        });
    },
    sublime9() {
      this.disabled9 = !this.disabled9;
    },
    // 监听数据
    onChange() {
      const { videoScore, funScore, demoScore } = this;
      this.$emit("change", {
        videoScore,
        funScore,
        demoScore,
      });
    },
    
  },
  watch: {
    scoring3: function() {
      if (this.scoring3<5 && this.scoring3>=1) {
        this.showfeedback = true;
        this.isNotClick =true;
      } else {
        this.showfeedback = false;
        this.isNotClick =false;
      }
      if(this.scoring3==5){
        this.isNotClick =true;
      }
    },
    videoScore: "onChange",
    funScore: "onChange",
    demoScore: "onChange",
  },
  mounted() {
    const { query } = this.$route;
    console.log('query',query);
    if(query.lang ==='en'){
      this.isEn = true;
    }
    if (query && query.uid) { 
      console.log('query',query);
    } else if(location.search){
       let search = location.search.substr(1);
       let params = search.split('&');
        params.forEach(item=>{
           if(item){
              query[item.split('=')[0]] = item.split('=')[1]
           }
        }) 
    }else{
      console.error('url formatter error!');
      return false;
    }

      this.uid = query.uid;
      this.roomid = query.room_id;
      this.name = query.name;
      this.product = query.product;
      this.platform = parseInt(query.platform);
      this.appVersionStr = query.app_version_string;
      this.appVersion = parseInt(query.app_version_string);

    this.query = query;
    
    query.lang && (this.$i18n.locale = query.lang);
    console.log('this.$i18n.locale',this.$i18n.locale);
    //  debugger;
    //  console.log('query',query);
    this.scoreDetailList = [
        this.$t('scoring.index.5a1htpgs7uk0'),
        this.$t('scoring.index.5a1htpgs7y80'),
        this.$t('scoring.index.5a1htpgs81c0'),
        this.$t('scoring.index.5a1htpgs8500'),
      ];
  },
};
</script>

